import toastr from 'toastr';

export class ToastrService {
    constructor() {}

    speak(info, title) {
        toastr.info(info, title)
    }

    edit(object) {
        toastr.success('a t mis  jour.', object.toString()); 
    }

    create(object) {
        toastr.info('a t cr.', object.toString());
    }

    remove(object) {
        toastr.info('a t supprim.', object.toString());
    }
}